<template>
  <div id="home-container">
    <h1>{{ msg }}</h1>
    <div>
      {{ newWord }}
    </div>
    <div class="tech-stack-container">
      <div class="transition-element" v-for="path in techStackArray" :key="path.path">
        <tech-stack :src="require(`../../assets/images/tech-stack/${path.path}`)"/>
      </div>
    </div>
  </div>
</template>

<script>
import TechStack from './tech-stack/TechStack'

export default {
  components: {
    'tech-stack': TechStack
  },

  data () {
    return {
      msg: 'Portfolio',
      newWord: 'I have a few repository with ready application',
      techStackArray: [
        { path: 'ts.png' },
        { path: 'JS.png' },
        { path: 'React.png' },
        { path: 'vue.png' },
        { path: 'webpack.png' },
        { path: 'sass.png' }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
#home-container {
  height: 100%;
}

.tech-stack-container {
  display: flex;
  overflow: auto;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



