<template>
  <div id="tech-stack">
     <b-card
        overlay
        :img-src="src"
        img-alt="tech stack"
        text-variant="white"
        :title="title"
      >
        <!-- <b-card-text>
          Some quick example text to build on the card and make up the bulk of the card's content.
        </b-card-text> -->
      </b-card>
  </div>
</template>

<script>
import { BCard, BCardText } from 'bootstrap-vue'

export default {
  components: {
    'b-card': BCard,
    'b-card-text': BCardText
  },
  props: {
    title: { type: String, required: false },
    src: { type: String, required: true }
  }
}
</script>

<style scoped>
#tech-stack {
  height: 100%;
}

.card {
  height: 100%;
}

.card-img {
  min-width: 310px;
  height: 100%;
}
</style>



